.main{
    background: #1E1E1E;
    min-height: 70vh;
    display: flex;
    /*flex-direction: row;*/
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1vh 3vh;

    padding-bottom: 0;
}


.img{
    max-width: 500px;
    /*max-height: 30vh;*/
    border: 2px #1E1E1E solid;
}
.img:hover{
    border: 2px white solid;
}



@media screen and (max-width: 1032px){
    .main {
        padding-bottom: 20px;
    }
}


@media screen and (max-width: 595px) {
    .img{
        max-width: 400px;
    }
}
@media screen and (max-width: 495px) {
    .img{
        max-width: 300px;
    }
}
@media screen and (max-width: 395px) {
    .img{
        max-width: 250px;
    }
    .main{
        align-items: start;
        gap: 0;
        min-height: 60vh;

    }
}
@media screen and (max-width: 340px) {
    .img{
        max-width: 200px;
    }
    .main {
        min-height: 50vh;
    }
}

@media screen and (max-width: 290px) {
    .img{
        max-width: 180px;
    }
    .main {
        min-height: 40vh;
    }
}

